html {
	height: 100%;
}
body {
	height: 100%;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	margin: 0px;
}
body * {
	font-family: 'NanumBarunGothic', sans-serif;
}
#root {
	text-align: center;
	min-height: 100%;
	position: relative;    
}

#home-main-container {
	text-align: center;
	/* background-image: url('../assets/page_main-1.jpg'); */
	background-repeat: round;
	max-width: 400px;
	margin: 0 auto;
	display: flex;
    flex-direction: column;
}

#home-main-container #header-bar {
	background-color: #000000;
	height: 24px;
	border: none;
	margin: 0;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
}

#home-main-container #content-container {
    margin-top: 40px;
    text-align: center;
}


#home-main-container #main-text {
	margin-top: 90px;
    font-size: 28px;
    font-weight: 600;
    color: #000000
}

#home-main-container #sub-text {
    margin-top: 38px;
    font-size: 18px;
	font-weight: 600;
	color: #000000;
	line-height: 28px;
}

#home-main-container #button-container {
    margin-top: 50px;
}

#home-main-container #btn-start-test {
    width: 235px;
    height: 72px;
    cursor: pointer;
    border: none;
	outline: none;
	background: url(/static/media/start.0ec32efb.png);
	background-repeat: round;
}

#home-main-container #logo-container {
	margin: 60px 0;
}

#home-main-container #logo {
    width: 115px;
	height: 80px;
}

#home-main-container #footer-bar {
	background-color: #8f1ba4;
	height: 24px;
	border: none;
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	margin: 0;
}

/* 575 이상 넘어가는 사이즈 */
@media screen and (min-width: 575px) {
	#home-main-container #main-text {
		font-size: 38px;
	}

	#home-main-container #sub-text {
		margin-top: 27px;
		font-size: 24px;
		line-height: 29px;
	}

	#home-main-container #btn-start-test {
		width: 360px;
		height: 100px;
	}

	#home-main-container #logo {
		width: 130px;
		height: 100px;
	}
}

#test-main-container {
    text-align: center;
    flex:1 1;
    flex-direction: column;
    align-items: center;
    /* background-image: url('../assets/page_test-1.jpg'); */
	background-repeat: round;
    max-width: 400px;
    margin: 0 auto;
}

#test-main-container #header-bar {
	background-color: #8f1ba4;
	height: 24px;
	border: none;
	margin: 0;
}

#test-main-container #progress-text-container {
    font-weight: 500;
    font-size: 22px;
    color: #000;
    text-align: left;
    margin: 13px 0 10px 22px;
}

#test-main-container .description {
    text-align: center;
    font-size: 21px;
    font-weight: 700;
    line-height: 1.6;
    padding: 0 30px;
    margin-top: 40px;
    white-space: pre-line;
}

#test-main-container .button-container {
    margin-top: 32px;
    text-align: center;
}

#test-main-container .test-button {
    width: 76%;
    max-width: 315px;
    color: #FFFFFF;
    background-color: #000000;
    height: 70px;
    font-size: 18px;
    font-weight: 600;
    line-height: 1.6;
    align-items: center;
    border-radius: 15px;
    margin-bottom: 22px;
    cursor: pointer;
    border: none;
    outline: none;
    white-space: pre-line;
    box-shadow: 5px 5px 4px #878787;
}

#test-main-container .image {
    width: 85%;
    margin-top: 20px;
}
#loading-container {
    position:absolute;
    top:45%;
    left:50%;
    max-width: 200px;
    -webkit-transform:translate(-50%, -50%);
            transform:translate(-50%, -50%);
}

#loading-text {
    margin-top: 30px;
    font-size: 20px;
    font-weight: 500;
}

#result-main-container {
    text-align: center;
	/* background-image: url('../assets/page_main-1.jpg'); */
	background-repeat: round;
	max-width: 400px;
	margin: 0 auto;
	display: flex;
    flex-direction: column;
}

#result-main-container #header-bar {
	background-color: #8f1ba4;
	height: 24px;
	border: none;
	margin: 0;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
}

#result-main-container #content-container {
    margin-top: 74px;
    padding: 0 20px;
    text-align: center;
}

#result-main-container #logo-container {
    margin-top: 20px;
    margin-bottom: 15px;
}

#result-main-container #friend-container {
    margin-top: 20px;
}

#result-main-container #friend-container .left {
    display: inline-block;
    vertical-align: middle;
    font-family: 'Nanum Gothic';
    font-weight: bold;
    line-height: 25px;
}

#result-main-container #friend-container .center {
    display: inline-block;
    vertical-align: middle;
    border: none;
    border-left: 2px dotted #8f1ba4;
    height: 80px;
    width: 1px;
    margin: 0 30px;
}

#result-main-container #friend-container .right {
    display: inline-block;
    vertical-align: middle;
    max-width: 80px;
    max-height: 70px;
    margin-top: -20px;
    font-weight: 400;
}

#result-main-container #friend-container .right #img-friend {
    width: 85%;
    height: 100%;
}

#result-main-container #logo {
    width: 80%;
    max-height: 300px;
}

#result-main-container #main-text {
    font-size: 28px;
    line-height: 1.5;
    font-weight: 700;
    white-space: pre-line;
}

#result-main-container #sub-text {
    width: 75%;
    display: inline-block;
    text-align: left;
    font-size: 15px;
    font-weight: 400;
    white-space: pre-line;
}

#result-main-container #share-container {
    margin-top: 40px;
}

#result-main-container #share-container a {
    -webkit-tap-highlight-color: transparent;
}

#result-main-container #share-container button {
    -webkit-tap-highlight-color: transparent;
}

#result-main-container #share-text {
    font-size: 20px;
    font-weight: 700;
}

#result-main-container #div-share-button1 {
    margin-top: 5px;
}

#result-main-container #div-share-button1 .share-button {
    width: 40px;
    height: 40px;
    cursor: pointer;
    border: none;
    outline: none;
    margin: 5px 10px;
    background-repeat: round;
}

#result-main-container #btn-share-url {
    width: 40px !important;
    background: url(/static/media/icon_share.1f59741c.png);
}

#result-main-container #btn-share-kakao {
    background: url(/static/media/icon_kakao.e5a8a70c.svg);
}

#result-main-container #btn-share-facebook {
    background: url(data:image/png;base64,);
    border-radius: 13px;
}

#result-main-container #btn-share-twitter {
    background: url(data:image/png;base64,);
    border-radius: 13px;
}

#result-main-container #div-share-button2 {
    margin-top: 17px;
}

#result-main-container #btn-restart-test {
    width: 110px;
    height: 38px;
    cursor: pointer;
    border: none;
    outline: none;
    background: url(/static/media/restart.b478c914.png);
    background-repeat: round;
    padding-bottom: 1px;
    margin: 0px 6px;
}

#result-main-container #div-share-button2 .share-button {
    cursor: pointer;
    border: none;
    outline: none;
    margin: 0px 10px;
    background-repeat: round;
}

#result-main-container #btn-share-againschool {
    background: url(/static/media/logo.ee3e92d2.png);
    width: 47px;
    height: 40px;
}

#result-main-container #btn-share-instargram {
    background: url(/static/media/icon_instargram.e2016f01.png);
    width: 40px;
    height: 40px;
    padding-bottom: 3px;
}

#result-main-container #btn-share-appstore {
    background: url(/static/media/appstore.0126e302.png);
    width: 40px;
    height: 40px;
    padding-bottom: 5px;
} 

#result-main-container #btn-share-playstore {
    background: url(/static/media/playstore.284af4ee.png);
    width: 40px;
    height: 40px;
    padding-bottom: 5px;
} 

#result-main-container #apply-container {
    margin: 50px 0;
}

#result-main-container #img-apply {
    width: 88%;
}

#result-main-container #footer-bar {
	background-color: #000000;
	height: 24px;
	border: none;
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	margin: 0;
}
