#loading-container {
    position:absolute;
    top:45%;
    left:50%;
    max-width: 200px;
    transform:translate(-50%, -50%);
}

#loading-text {
    margin-top: 30px;
    font-size: 20px;
    font-weight: 500;
}

#result-main-container {
    text-align: center;
	/* background-image: url('../assets/page_main-1.jpg'); */
	background-repeat: round;
	max-width: 400px;
	margin: 0 auto;
	display: flex;
    flex-direction: column;
}

#result-main-container #header-bar {
	background-color: #8f1ba4;
	height: 24px;
	border: none;
	margin: 0;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
}

#result-main-container #content-container {
    margin-top: 74px;
    padding: 0 20px;
    text-align: center;
}

#result-main-container #logo-container {
    margin-top: 20px;
    margin-bottom: 15px;
}

#result-main-container #friend-container {
    margin-top: 20px;
}

#result-main-container #friend-container .left {
    display: inline-block;
    vertical-align: middle;
    font-family: 'Nanum Gothic';
    font-weight: bold;
    line-height: 25px;
}

#result-main-container #friend-container .center {
    display: inline-block;
    vertical-align: middle;
    border: none;
    border-left: 2px dotted #8f1ba4;
    height: 80px;
    width: 1px;
    margin: 0 30px;
}

#result-main-container #friend-container .right {
    display: inline-block;
    vertical-align: middle;
    max-width: 80px;
    max-height: 70px;
    margin-top: -20px;
    font-weight: 400;
}

#result-main-container #friend-container .right #img-friend {
    width: 85%;
    height: 100%;
}

#result-main-container #logo {
    width: 80%;
    max-height: 300px;
}

#result-main-container #main-text {
    font-size: 28px;
    line-height: 1.5;
    font-weight: 700;
    white-space: pre-line;
}

#result-main-container #sub-text {
    width: 75%;
    display: inline-block;
    text-align: left;
    font-size: 15px;
    font-weight: 400;
    white-space: pre-line;
}

#result-main-container #share-container {
    margin-top: 40px;
}

#result-main-container #share-container a {
    -webkit-tap-highlight-color: transparent;
}

#result-main-container #share-container button {
    -webkit-tap-highlight-color: transparent;
}

#result-main-container #share-text {
    font-size: 20px;
    font-weight: 700;
}

#result-main-container #div-share-button1 {
    margin-top: 5px;
}

#result-main-container #div-share-button1 .share-button {
    width: 40px;
    height: 40px;
    cursor: pointer;
    border: none;
    outline: none;
    margin: 5px 10px;
    background-repeat: round;
}

#result-main-container #btn-share-url {
    width: 40px !important;
    background: url('../assets/icon_share.png');
}

#result-main-container #btn-share-kakao {
    background: url('../assets/icon_kakao.svg');
}

#result-main-container #btn-share-facebook {
    background: url('../assets/icon_facebook.png');
    border-radius: 13px;
}

#result-main-container #btn-share-twitter {
    background: url('../assets/icon_twitter.png');
    border-radius: 13px;
}

#result-main-container #div-share-button2 {
    margin-top: 17px;
}

#result-main-container #btn-restart-test {
    width: 110px;
    height: 38px;
    cursor: pointer;
    border: none;
    outline: none;
    background: url('../assets/restart.png');
    background-repeat: round;
    padding-bottom: 1px;
    margin: 0px 6px;
}

#result-main-container #div-share-button2 .share-button {
    cursor: pointer;
    border: none;
    outline: none;
    margin: 0px 10px;
    background-repeat: round;
}

#result-main-container #btn-share-againschool {
    background: url('../assets/logo.png');
    width: 47px;
    height: 40px;
}

#result-main-container #btn-share-instargram {
    background: url('../assets/icon_instargram.png');
    width: 40px;
    height: 40px;
    padding-bottom: 3px;
}

#result-main-container #btn-share-appstore {
    background: url('../assets/appstore.png');
    width: 40px;
    height: 40px;
    padding-bottom: 5px;
} 

#result-main-container #btn-share-playstore {
    background: url('../assets/playstore.png');
    width: 40px;
    height: 40px;
    padding-bottom: 5px;
} 

#result-main-container #apply-container {
    margin: 50px 0;
}

#result-main-container #img-apply {
    width: 88%;
}

#result-main-container #footer-bar {
	background-color: #000000;
	height: 24px;
	border: none;
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	margin: 0;
}